.container {
  max-width: 1450px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;

  @media (max-width: $desktop-width) {
    max-width: 100%;
  }

  @media (max-width: $tablet-width) {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.container--company,
.container--production,
.container--form {
  padding-left: 25px;
  padding-right: 25px;
  padding-top: 109px;

  @media (max-width: $desktop-width) {
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 50px;
  }

  @media (max-width: $tablet-width) {
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 38px;
  }
}

.container--production {
  padding-top: 93px;

  @media (max-width: $desktop-width) {
    padding-top: 53px;
  }
}

.container--form {
  padding-top: 96px;
  padding-bottom: 113px;

  @media (max-width: $desktop-width) {
    padding-top: 83px;
    padding-bottom: 103px;
  }

  @media (max-width: $tablet-width) {
    padding-top: 33px;
    padding-bottom: 40px;
  }
}
